import React from 'react'
import  { useStaticQuery , graphql ,Link} from 'gatsby'


const OurBlogComponent = ({addClass}) => {
	const data = useStaticQuery(graphql`
	query{
		bh1: file(relativePath: {eq:"blog/bh1.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		bh2: file(relativePath: {eq:"blog/bh2.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		bh3: file(relativePath: {eq:"blog/bh3.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		
		pposter1: file(relativePath: {eq: "property/pposter1.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		  }	
	}`
)
	return (
		<section className={`our-blog ${addClass} pb30`}>
  			<div className="container">
				<div className="row">
				  	<div className="col-lg-6 offset-lg-3">
						<div className="main-title text-center">
						  	<h2>Articles &amp; Tips</h2>
						  	<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
						</div>
				  	</div>
				</div>
				<div className="row">
	  				<div className="col-md-6 col-lg-4 col-xl-4">
						<Link to="/blog/single/">
						<div className="for_blog feat_property">
						  	<div className="thumb">
								<img className="img-whp"  src={data.bh1.childImageSharp.fluid.originalImg} alt="bh1.jpg" />
						  	</div>
						  	<div className="details">
								<div className="tc_content">
							  		<p className="text-thm">Business</p>
							  		<h4>Skills That You Can Learn In The Real Estate Market</h4>
								</div>
								<div className="fp_footer">
							  		<ul className="fp_meta float-left mb0">
										<li className="list-inline-item"><span ><img src={data.pposter1.childImageSharp.fluid.originalImg} alt="pposter1.png" /></span></li>
										<li className="list-inline-item"><span >Ali Tufan</span></li>
							  		</ul>
							  		<span className="fp_pdate float-right" >7 August 2019</span>
								</div>
						  	</div>
						</div>
					</Link>
	  				</div>
	  				<div className="col-md-6 col-lg-4 col-xl-4">
						<Link to="/blog/single/">
						<div className="for_blog feat_property">
						  	<div className="thumb">
								<img className="img-whp" src={data.bh2.childImageSharp.fluid.originalImg} alt="bh2.jpg" />
						  	</div>
						  	<div className="details">
								<div className="tc_content">
							  		<p className="text-thm">Business</p>
							  		<h4>Bedroom Colors You’ll Never <br className="dn-1199" /> Regret</h4>
								</div>
								<div className="fp_footer">
							  		<ul className="fp_meta float-left mb0">
										<li className="list-inline-item"><span ><img src={data.pposter1.childImageSharp.fluid.originalImg} alt="pposter1.png" /></span></li>
										<li className="list-inline-item"><span >Ali Tufan</span></li>
							  		</ul>
							  		<span className="fp_pdate float-right" >7 August 2019</span>
								</div>
						  	</div>
						</div>
						</Link>
					  </div>
						<div className="col-md-6 col-lg-4 col-xl-4">
							<Link to="/blog/single/">
							<div className="for_blog feat_property">
								<div className="thumb">
									<img className="img-whp" src={data.bh3.childImageSharp.fluid.originalImg}alt="bh3.jpg" />
								</div>
								<div className="details">
									<div className="tc_content">
										<p className="text-thm">Business</p>
										<h4>5 Essential Steps for Buying an Investment</h4>
									</div>
									<div className="fp_footer">
										<ul className="fp_meta float-left mb0">
											<li className="list-inline-item"><span ><img src={data.pposter1.childImageSharp.fluid.originalImg} alt="pposter1.png" /></span></li>
											<li className="list-inline-item"><span >Ali Tufan</span></li>
										</ul>
										<span className="fp_pdate float-right" >7 August 2019</span>
									</div>
								</div>
							</div>
							</Link>
						</div>
					</div>
  				</div>
		</section>
	)
}

export default OurBlogComponent;